a {
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  -webkit-tap-highlight-color: transparent;
  *outline: 0;
}

.btn {
  position: relative;
  background-color: transparent;
  border: none;
  border-radius: 0.875rem;
  font-size: 1rem;
  line-height: 1.25rem;
  font-weight: bold;
  padding: 0.875rem 2rem;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  -webkit-tap-highlight-color: transparent;
  *outline: 0;
  overflow: hidden;
}
@media screen and (max-width: 539px) {
  .btn {
    font-size: 0.875rem;
    line-height: 1.125rem;
  }
}
@media screen and (max-width: 531px) {
  .btn {
    border-radius: 0.625rem;
    padding: 0.5625rem 1.5625rem;
  }
}
.btn__bordered {
  border: 0.125rem solid #606060;
}
@media screen and (min-width: 1200px) {
  .btn__bordered {
    position: relative;
    transition: all 0.5s cubic-bezier(0.22, 0.44, 0, 1);
  }
  .btn__bordered::before, .btn__bordered::after {
    position: absolute;
    left: 50%;
    content: "";
    background-color: rgba(96, 96, 96, 0);
    border-radius: 50%;
    transform: translate(-50%, -50%);
    height: 0px;
    width: 0px;
    transition: all 0.5s cubic-bezier(0.22, 0.44, 0, 1);
    transition-duration: 0.5s;
    z-index: -1;
  }
  .btn__bordered::before {
    top: 110%;
    transition-delay: 0.1s;
  }
  .btn__bordered::after {
    top: -10%;
  }
  .btn__bordered:hover::before, .btn__bordered:hover::after {
    height: 300px;
    width: 300px;
    transition-duration: 1s;
  }
  .btn__bordered:hover::before {
    background-color: #606060;
  }
  .btn__bordered:hover::after {
    background-color: rgba(96, 96, 96, 0.5);
  }
}
.btn__bordered-white {
  border: 0.125rem solid #FFFFFF;
}
@media screen and (min-width: 1200px) {
  .btn__bordered-white {
    position: relative;
    transition: all 0.5s cubic-bezier(0.22, 0.44, 0, 1);
    z-index: 1;
  }
  .btn__bordered-white::before, .btn__bordered-white::after {
    position: absolute;
    left: 50%;
    content: "";
    background-color: rgba(255, 255, 255, 0);
    border-radius: 50%;
    transform: translate(-50%, -50%);
    height: 0px;
    width: 0px;
    transition: all 0.5s cubic-bezier(0.22, 0.44, 0, 1);
    transition-duration: 0.5s;
    z-index: -1;
  }
  .btn__bordered-white::before {
    top: 110%;
    transition-delay: 0.1s;
  }
  .btn__bordered-white::after {
    top: -10%;
  }
  .btn__bordered-white:hover {
    color: #3b3b3b;
  }
  .btn__bordered-white:hover::before, .btn__bordered-white:hover::after {
    height: 300px;
    width: 300px;
    transition-duration: 1s;
  }
  .btn__bordered-white:hover::before {
    background-color: white;
  }
  .btn__bordered-white:hover::after {
    background-color: rgba(255, 255, 255, 0.5);
  }
}
.btn__about-video {
  background-image: url("../images/btn-bg-about-video.png");
  background-color: rgba(45, 45, 45, 0.6);
  background-position: center;
  background-size: 100%;
  background-repeat: no-repeat;
  background-blend-mode: multiply;
  padding: 0.875rem 2rem;
  box-shadow: 0 0.875rem 1.25rem rgba(0, 0, 0, 0.2);
  z-index: 1;
}
@media screen and (max-width: 539px) {
  .btn__about-video {
    padding: 0.6875rem 1.375rem;
  }
}
.btn__about-video::before {
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  background-image: url("../images/video-noise.gif");
  background-position: center;
  background-size: 100%;
  background-repeat: no-repeat;
  opacity: 0;
  z-index: -1;
  height: 100%;
  width: 100%;
}
.btn__about-video > span {
  margin-left: 2.5rem;
