    height: 1.375rem;
    width: 1.375rem;
  }
}
@media screen and (min-width: 1200px) {
  .btn__about-video {
    transition: all 0.5s cubic-bezier(0.22, 0.44, 0, 1);
    transition-property: background-size;
  }
  .btn__about-video:hover {
    background-color: rgba(45, 45, 45, 0);
    background-size: 110%;
  }
  .btn__about-video:hover::before {
