.btn__with-arrow {
  color: #9F9F9F;
  transition: all 0.5s cubic-bezier(0.22, 0.44, 0, 1);
  padding: 0.875rem 0;
}
.btn__with-arrow .arrow-wrapper {
  position: relative;
  display: inline-block;
  margin-left: 0.375rem;
  height: 0.75rem;
  width: 2.125rem;
  overflow: hidden;
}
.btn__with-arrow .arrow-wrapper .icon-svg {
  position: absolute;
  top: 0;
  left: -4.25rem;
  fill: #9F9F9F;
  display: inline-block;
  height: 0.75rem;
  width: 2.125rem;
}
.btn__with-arrow .arrow-wrapper .icon-svg:first-of-type {
  left: 0;
}
@media screen and (min-width: 1200px) {
  .btn__with-arrow:hover {
    color: #FFFFFF;
  }
  .btn__with-arrow:hover .arrow-wrapper .icon-svg {
    left: 0;
    transition: all 0.5s cubic-bezier(0.22, 0.44, 0, 1);
  }
  .btn__with-arrow:hover .arrow-wrapper .icon-svg:first-of-type {
    left: 4.25rem;
  }
